import styled from "styled-components";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

const Container = styled.div`
  background-color: #FCF6F4;
  width: 900px;
  height: auto;
  border: solid 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;

  @media (max-width: 500px) {
    width: 80vw;
    height: auto;
  }
`;

const ImgContainer = styled.div`
    cursor: pointer;
`;

const Body = styled.div`
  @media (max-width: 500px) {
    margin: 0 0 25px 0;
  }
`;

const Footer = styled.div`
  margin: 10px 0 0 0;
`;

const ProjectComponent = (props) => {
    const { name, tags, description, fronturl, backurl, fullurl, imgSrc1, imgSrc2, imgSrc3, imgSrc4, link } = props.project;
    return (
        <Container>
            <Body>
                <Carousel>
                    <ImgContainer onClick={() => { window.location = link }}>
                        <img src={imgSrc1} alt="img" />
                    </ImgContainer>
                    <ImgContainer onClick={() => { window.location = link }}>
                        <img src={imgSrc2} alt="img" />
                    </ImgContainer>
                    <ImgContainer onClick={() => { window.location = link }}>
                        <img src={imgSrc3} alt="img" />
                    </ImgContainer>
                    <ImgContainer onClick={() => { window.location = link }}>
                        <img src={imgSrc4} alt="img" />
                    </ImgContainer>
                </Carousel>
                <h2>{name}</h2>
                <p>{description}</p>
            </Body>
            <Footer>
                <hr />
                <p>{tags}</p>
                {backurl ?
                    <div>
                        <a href={backurl}>Repositório Frontend</a>
                    </div> : null}
                {fronturl ?
                    <div>
                        <a href={fronturl}>Repositório Backend</a>
                    </div> : null}
                {fullurl ?
                    <div>
                        <a href={fullurl}>Repositório Fullstack</a>
                    </div> : null}

            </Footer>
        </Container>
    );
};

export default ProjectComponent;
